import { Component, OnInit, AfterContentChecked } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-articles-preview',
  templateUrl: './articles-preview.component.html',
  styleUrls: ['./articles-preview.component.css']
})
export class ArticlesPreviewComponent implements OnInit, AfterContentChecked {

  active = false;
  articles = [];
  pureTextContent = [];

  constructor(
    private http: HttpClient,
    private sanitizer: DomSanitizer,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.route.queryParams.subscribe(param => {
      if (param.tags) {
        this.getArticlesByTags(param.tags).subscribe(articles => {
          console.log('articles by tags: ', articles);
          this.articles = articles;
        });
      } else {
        this.getArticles().subscribe(articles => {
          console.log("TCL: ArticlesPreviewComponent -> ngOnInit -> articles", articles)
          this.articles = articles;
        });
      }
    });
  }
  ngAfterContentChecked(): void {
    const hiddenHtml = document.getElementsByClassName('hidden-html');
    if (this.active === false) {
      for (let i = 0; i < hiddenHtml.length; i++) {
        if (hiddenHtml[i].textContent.length > 81) {
          this.pureTextContent.push(hiddenHtml[i].textContent.slice(0, 80) + ' ...');
        } else {
          this.pureTextContent.push(hiddenHtml[i].textContent);
        }
        this.active = true;
      }
    }
  }

  getArticles(): Observable<any> {
    return this.http.get<any>('/api3/articles/activated');
  }

  getArticlesByTags(tags): Observable<any> {
    return this.http.get<any>(`/api3/articles/activated?tags=${tags}`);
  }

  getBackground(image: string) {
    return this.sanitizer.bypassSecurityTrustStyle(`url(${image})`);
  }
}
