import { Component, AfterViewInit, ViewEncapsulation, OnInit } from '@angular/core';
import { ArticleService } from '../article.service';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

@Component({
  selector: 'app-article-preview',
// tslint:disable-next-line: max-line-length
  // template: '<mat-toolbar><button mat-button *ngIf="articleid !== null" (click)="backTo()">Back</button><span>Newsletter</span><span class="space-toolbar"></span></mat-toolbar><div class="article-preview"><quill-editor [(ngModel)]="htmlText" class="article-preview-content"></quill-editor></div>',
  template: '<div class="article-preview"><quill-editor [(ngModel)]="htmlText" class="article-preview-content"></quill-editor></div>',
  styleUrls: ['./article-preview.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class ArticlePreviewComponent implements OnInit, AfterViewInit {

  constructor(
    private articleService: ArticleService,
    private http: HttpClient,
    private route: ActivatedRoute,
    private location: Location,
    ) { }

  articleid = '';
  htmlText = '<p></p>';
  ngOnInit(): void {
    this.articleid = this.route.snapshot.paramMap.get('articleid');
    if (this.articleid !== null) {
      this.getArticles(this.articleid).subscribe(article => {
        this.htmlText = article.content;
      });
    } else {
      this.htmlText = this.articleService.getArticleHtmlText();
    }
  }

  ngAfterViewInit(): void {
    document.getElementsByClassName('ql-editor')[0].attributes[2].value = 'false';
  }

  getArticles(articleid: string): Observable<any> {
    return this.http.get<any>(`/api3/articles/${articleid}`);
  }

  backTo(): void {
    this.location.back();
  }
}
