import { Injectable } from '@angular/core';
@Injectable({
  providedIn: 'root'
})
export class ArticleService {

  constructor() { }

  setArticleHtmlText(htmlText: string) {
    return localStorage.setItem('article', htmlText);
  }

  getArticleHtmlText() {
    return localStorage.getItem('article');
  }

  setArticles(articles: string) {
    return localStorage.setItem('articles', articles);
  }

  getArticles() {
    return localStorage.getItem('articles');
  }
}
